@import url('http://fonts.cdnfonts.com/css/poppins');

* {
  margin: 0;
}

Body {
  font-family: 'Poppins', sans-serif;
}

nav {
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.grey {
  color: #918E9B;
}

.bold {
  font-weight: bold;
}

.nav--logo {
  max-width: 300px;
}

section {
  padding: 20px;
}

.hero {
  display: flex;
  flex-direction: column;
}

.hero--photo {
  max-width: 400px;
  align-self: center;
}

.hero--header {
  margin-top: 16px;
  margin-bottom: 16px;
}

.hero--text {
  margin-top: 0px;
}


.card {
  width: 175px;
  font-size: 12px;
  margin: 10px;
}

.card--image {
  width: 100%;
  border-radius: 9px;
}

.card--stats {
  display: flex;
  align-items: center;
}


.card--star {
  height: 14px;
}


p {
  margin-top: 5px;
}